export default {
    /**--------------------------math-start----------------------**/
    /**
     * toPoint - 百分数转化为小数
     * @param percent
     */
    toPoint: function (percent) {
        var str = percent.replace('%', '');
        // @ts-ignore
        return str / 100;
    },
    /**
     * point - 小数转化为百分数
     * @param point
     */
    toPercent: function (point) {
        var str = Number(point * 100).toFixed(1);
        str += '%';
        return str;
    },
    /**
     * straightLineIntersection - 计算两个直线的交点
     * @param p1
     * @param p2
     * @param p3
     * @param p4
     */
    straightLineIntersection: function (p1, p2, p3, p4) {
        var x1 = p1.x, y1 = p1.y;
        var x2 = p2.x, y2 = p2.y;
        var x3 = p3.x, y3 = p3.y;
        var x4 = p4.x, y4 = p4.y;
        return {
            x: ((x3 - x4) * (x2 * y1 - x1 * y2) - (x1 - x2) * (x4 * y3 - x3 * y4)) /
                ((x3 - x4) * (y1 - y2) - (x1 - x2) * (y3 - y4)),
            y: ((y3 - y4) * (y2 * x1 - y1 * x2) - (y1 - y2) * (y4 * x3 - y3 * x4)) /
                ((y3 - y4) * (x1 - x2) - (y1 - y2) * (x3 - x4)),
        };
    },
};
